.form {
    position: relative;
    width: 280px;
    // margin-bottom: 20px;   Вертикальні марджини різні для різних сторінок
    margin-left: auto;
    margin-right: auto;
    border-radius: 24px;
    box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
}

.input {
    width: 100%;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 20px;
    padding-right: 80px;
    border: none;
    border-radius: 24px;

    font-family: 'Inter';
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.04em;

    color: #888888;
}

.clearBtn {
    width: 24px;
    height: 24px;
    padding: 0;
    border: none;

    position: absolute;
    right: 13px;
    top: 10px;

    line-height: 0;
    background-color: transparent;
}

.clearBtnIcon {
    stroke: #ffc107;
}

.submitBtn {
    width: 24px;
    height: 24px;
    padding: 0;
    border: none;

    position: absolute;
    right: 12px;
    top: 10px;

    line-height: 0;
    background-color: transparent;

    &.active {
        right: 47px;
    }
}

@media screen and (min-width: 768px) {
    .form {
        width: 608px;
    }

    .input {
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 20px;
        line-height: 24px;
    }

    .submitBtn {
        right: 20px;
    }
}
